import React, {useState } from "react";
import '../../src/styles/exp.css'

const Experience =  () => {

    const [position, setPosition] = useState('Position')
    const [company, setCompany] = useState('Company')
    const [city, setCity] = useState('City')
    const [from, setFrom] = useState('dd/mm/yyyy')
    const [to, setTo] = useState('dd/mm/yyyy')
    const [desc,setDesc] = useState()

    const [change, setChange] = useState(false)

    return (() => {

        if(change !== undefined){
            if(!change){
            return(
                <div>
                <input 
                    name='position'
                    placeholder='Poaition'
                    onChange={e => setPosition(e.target.value)}
                    value={position} 
                    type='text'
                    className='inp'
                    />
                    <br/>

                    <input 
                    name='company'
                    placeholder='Company'
                    onChange={e => setCompany(e.target.value)}
                    value={company}
                    type='text'
                    className='inp'
                    />
                    <br/>

                    <input 
                    name='city'
                    placeholder='City'
                    onChange={e => setCity(e.target.value)}
                    value={city}
                    type='text'
                    className='inp'
                    />
                    <br/>

                    <label>from:</label>
                    <input 
                    name='from'
                    placeholder='from'
                    onChange={e => setFrom(e.target.value)}
                    value={from} 
                    type='date'
                    className='from-to'
                    />

                    <label>to:</label>
                    <input 
                    name='to'
                    placeholder='to'
                    onChange={e => setTo(e.target.value)}
                    value={to}
                    type='date'
                    className='from-to'
                    />
                    <br/>

                    <textarea type='text' 
                    name='desc' 
                    onChange={e => setDesc(e.target.value)}
                    placeholder='Description' 
                    value={desc} 
                    id='desc-inp'
                    />
                    <br/>

                    <button onClick={() => setChange(true)}>Done</button>

                </div>
            )
            }

            else{
                return(
                    <div>
                        <p>{position}</p>
                        <p>{company}</p>
                        <p>{city}</p>
                        <p>{from} to {to}</p>
                        <p id='desc'>{desc}</p>

                        <button onClick={() => setChange(false)}>Edit</button>
                        <button onClick={() => setChange(undefined)}>Delete</button>
                    </div>
                )
            }
        } else{return(null)}
    })();
}

export default Experience