import React, {Component} from 'react';
import Education from './Components/eduEx';
import Name from './Components/general';
import Experience from './Components/pracEx';
import '../src/app.css'
import Seperate from './Components/seperate';
import Header from './Components/header'

class App extends Component {
  constructor() {
    super();

    this.state = {
      exp: [],
      edu: []
    }
  }

  addExp = () => {
    this.setState({
      exp: [...this.state.exp, <Experience />]
    })
  }

  addEdu = () => {
    this.setState({
      edu: [...this.state.edu, <Education />]
    })
  }

  render() {
    return (
      <div id='container'>

        <Name />

        <Seperate />
        <p className='head'>Education:  </p>
        {this.state.edu}
        <button onClick={this.addEdu}>Add</button>
        <Seperate />
          
        <p className='head'>Experience:</p>
        {this.state.exp}
        <button onClick={this.addExp}>Add</button>
      </div>
      )
  }
} 

export default App;
