import React, { useState } from "react";
import '../../src/styles/edu.css'

const Education = () => {
    
    const [schoolName, setSchoolName] = useState('School Name');
    const [address, setAddress] = useState('Address');
    const [title, setTitle] = useState('Title');
    const [from, setFrom] = useState('dd/mm/yyyy');
    const [to, setTo] = useState('dd/mm/yyyy');
    const [desc,setDesc] = useState()

    const [change, setChange] = useState(false);

    return (() => {
        if(change !== undefined){
            if(!change){
                return(

                <div>
                <input
                placeholder='Title'
                name='title'
                type='text'
                onChange={e => setTitle(e.target.value)}
                value={title}
                className='inp'
                />
                <br/>

                <input
                placeholder='School Name'
                name='schoolName'
                type='text'
                onChange={e => setSchoolName(e.target.value)}
                value={schoolName}
                className='inp'
                />
                <br/>

                <input
                placeholder='Address'
                name='address'
                type='text'
                onChange={e => setAddress(e.target.value)}
                value={address}
                className='inp'
                />
                <br/>

                <label>from:</label>
                <input
                name='from'
                type='date'
                onChange={e => setFrom(e.target.value)}
                value={from}
                className='from-to'
                />  

                <label>to:</label>
                <input
                name='to'
                type='date'
                onChange={e => setTo(e.target.value)}
                value={to}
                className='from-to'
                />   
                <br/>

                <textarea type='text' 
                name='desc' 
                onChange={e => setDesc(e.target.value)}
                placeholder='Description' 
                value={desc} 
                id='desc-inp'
                />
                <br/>

                <button onClick={() => setChange(true)}>Done</button>
                </div>
                )
            }
            else{
                return(
                    <div>
                        <p>{title}</p>
                        <p>{schoolName}</p>
                        <p>{address}</p>
                        <p>{from} to {to}</p>
                        <br/>

                        <button onClick={() => setChange(false)}>Edit</button>
                        <button onClick={() => setChange(undefined)}>Delete</button>
                    </div>
                )
            }
        } else{return(null)}
    })();
}

export default Education