import React, {useState} from "react";
import '../../src/styles/general.css';

const Name = () => {
    
    const [userName,setUserName] = useState('Name');
    const [email,setEmail] = useState('email@mail.com');
    const [title,setTitle] = useState('Title');
    const [desc,setDesc] = useState('Description');

    const [change,setChange] = useState(false)

    return (() => {

        if(change === false){
            return(
            <div className='gen-infodisp'>
            <p id='username'>{userName}</p>
            <p id='title'>{title},</p>
            <p id='email'>{email}</p>
            <p id='desc'>{desc}</p>
            <button onClick={e => setChange(true)}>Edit</button>
            </div>
            )
        }

        else{
        return(
            <div>
                <form>
                    
                    <input type='text' 
                    name='userName' 
                    onChange={e => setUserName(e.target.value)}
                    placeholder='Name'
                    value={userName} 
                    id='name-input'
                    className='inp'
                    />
                    <br/>

                    <input type='text' 
                    name='title' 
                    onChange={e => setTitle(e.target.value)}
                    placeholder='Title' 
                    value={title} 
                    id='title-inp'
                    className='inp'
                    />
                    <br/>

                    <input type='text' 
                    name='email' 
                    onChange={e => setEmail(e.target.value)}
                    placeholder='Email'
                    value={email} 
                    className='inp'
                    />
                    <br/>

                    <textarea type='text' 
                    name='desc' 
                    onChange={e => setDesc(e.target.value)}
                    placeholder='Description' 
                    value={desc} 
                    id='desc-inp'
                    />
                    <br/>

                </form>

                <button onClick={e => setChange(false)}>Done</button>

            </div>
        );
        }
    })();
}

export default Name